.line-bar-progress {
  margin-bottom: 25px;
}

.skill-name {
  font-weight: bold;
  margin-bottom: 5px;
}

.progress-bar {
  position: absolute;
  height: 10px;
  width: 70%;
  background-color: #eee;
  border-radius: 5px;
  overflow: hidden;
}

.progress {
  height: 100%;
  width: 0;
  background-color: #04aa6d; /* Warna progress */
  border-radius: 5px;
  transition: width 1s ease-in-out; /* Animasi transisi lebar */
}
